<!---->
<template>
  <div class="Rectangle" style="box-sizing: border-box;margin-bottom: 15px">
    <div class="Mask" id="b0" v-if="!showNan">
      <div class="identifying" v-if="false">VIP5</div>
      <img
        :src='logo'
        :imgClass='HistoryImg'
      />
    </div>
    <div class="Maskyunan" id="b0" v-if="showNan">
      <img :src=(art.assetImg) >
    </div>
    <div class="Bona">
      <div class="ss">
        <div class="text-page">
          <div class="sl1">{{art.assetName}}</div><div class="sl2"></div>
        </div>
      </div>
      <p class="text-page2">
        <span>{{art.providerMemo.split(',')[0]}}</span>
        <span>{{art.score}}</span>
        <span>{{art.area}}</span>
        <span>{{art.year==0?'':art.year}}</span>
        <span>{{art.language}}</span>
        <!-- <span>{{art.episodes+""}}</span> -->
      </p>
      <div class="advert">
        <lazy-image
          :src="initScores"
          :placeholder="placeholder"
          :initScore = "initScore"
        />
      </div>
      <p class="content-page3">{{art.description}}</p>
      <div class="btn-page ">
        <div class="btn-sc" id="b1">
          <span v-if="sc" class="jlt2"></span>
          <span v-if="sc"></span><span v-if="!sc" class="jlt1"></span>
        </div>
        <!--<div class="btc-jd" id="b2"><i class="iconfont">&#xe60b    </i></div>-->
        <div class="btn-js " id="b2" v-if="showIntroduction"><span class="jlt3"></span></div>
      </div>

    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>

<style scoped="seriesTop">
.Rectangle .Maskyunan.change {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  border: solid 4px white;
  box-sizing: border-box;
}
.Maskyunan {
  border-radius: 15px;
  width: 270px;
  height: 385px;
  margin-left: 24px;
  float: left;
  position: relative;
}
.Maskyunan img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-sizing: border-box;
}
.Rectangle .Mask .identifying {
  position: absolute;
  top: 7px;
  right: 3px;
  width: 198px;
  height: 34px;
  border-radius: 0px 15px 0px 15px;
  background: #f8b621;
  color: #000000;
  font-size: 20px;
  line-height: 34px;
  font-family: Helvetica Neue, Bold Italic;
  z-index: 9;
}
.advert {
  width: 460px;
  height: 110px;
  margin-top: 31px;
  border-radius: 15px;
  overflow: hidden;
}
.advert img {
  width: 100%;
  height: 100%;
}
.Rectangle {
  width: 1221px;
  height: 380px;
}
.Rectangle:after {
  clear: both;
  content: "";
  display: block;
}
.Rectangle .Mask {
  position: relative;
  width: 683px;
  height: 389px;
  left: 0px;
  top: 0px;
  float: left;
  text-align: center;
  /* border: 2px solid #666666; */
}
.Rectangle .Mask img {
  width: 100%;
  height: 100%;
}
.Bona {
  position: relative;
  width: 460px;
  margin-left: 24px;
  margin-top: 6px;
  float: left;
}

.ss{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  width: 400;
}
.Bona .text-page {
  margin: 0px auto;
}

.text-page div {
  font-family: PingFangSC-Semibold;
  display: -webkit-box;
  font-size: 35px;
  color: #ececec;
  text-align: left;
  margin-right: 15px;
  line-height: 43px;
  display: inline-block;
}
.Bona .text-page .sl1{
  width: 455px;
  overflow: hidden;
  text-overflow: ellipsis
}
.Bona .text-page2 {
  font-family: Helvetica Neue;
  font-size: 20px;
  color: #ffffff;
  text-align: left;
  margin-top: 18px;
}
.Bona .text-page2:after {
  clear: both;
  content: "";
  display: block;
}
.Bona .text-page2 span {
  border-left: 3px solid white;
  text-indent: 10px;
  display: block;
  float: left;
  margin-right: 10px;
}
.Bona .text-page2 span:nth-child(1) {
  border: none;
  text-indent: 1px;
}
.Bona .content-page3 {
  font-family: PingFangSC-Regular;
  font-size: 20px;
  color: #ffffff;
  text-align: left;
  margin-top: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.btn-page {
  border-radius: 5px;
  width: 500px;
  height: 48px;
  position: absolute;
  top: 330px;
}
.btn-page div {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid white;
  font-family: PingFangSC-Regular;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  float: left;
  line-height: 48px;
  width: 30%;
  box-sizing: border-box;
  border-radius: 16px;
  margin-right: 8px;
}
.oll-page li {
  font-family: PingFangSC-Regular;
  font-size: 18px;
  color: #ffffff;
  float: left;
  padding-left: 42px;
  padding-top: 14px;
}
.Rectangle .btn-page .change {
  background: #0098bc;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  border: solid 4px white;
  margin-top: -2px;
}
.Rectangle .Mask.change {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  border: solid 4px white;
  box-sizing: border-box;
  /* margin-top: 4px;
      margin-left: 4px; */
}
.advert.change {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  border: solid 4px white;
  box-sizing: border-box;
  border-radius: 15px;
}

.jlt1 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -105px;
}
.jlt2 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -105px;
}

.jlt3 {
  width: 35px;
  height: 35px;
  padding: 5px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -315px;
}
</style>
<style>
.HistoryImg {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
}
</style>
<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import Toast from "../../tool/toast/toast.js";
import { xha } from "../../js/xha.js";
export default {
  props: ["initData", "jsonUrl", "isAuth", "isPreView","FilmType","assetInfo","productId"],
  name: "SeriesTop",
  data() {
    return {
      showIntroduction:false,
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      logo: require("../../../static/bg.png"),
      name: "seriesTop",
      itemNo: true,
      isShow: false,
      initScores: require("../../assets/img_loading_160x230.png"),
      num: 0,
      op: 1,
      itNum: 1,
      sc: false,
      ss: true,
      eleId: "",
      isEnd: false,
      HistoryImg: "HistoryImg",
      pos: "",
      showNan: "",
      timer: null,
      art: {
        description: "",
        elementId: "",
        assetImg: "",
        assetId: "",
        assetName: "",
        layout: "",
        score: "",
        area: "",
        year: "",
        episodes: "",
        language: "",
        providerMemo: ""
      },
      backTimes: 0,
      EnterType:localStorage.getItem('EnterType') || 1,
    };
  },
  created() {
    window.preViewCallback = this.preViewCallback;
    window.changeEpisode = this.changeEpisode;
    this.$nextTick(function() {
      this.init();
    });
  },
  mounted() {
    this.art = this.initData.data;
    this.$lazyImages.loadImage();
    // this.startScroll();
    this.$nextTick(function(){
      if(this.initData.data.description.length>45){
        this.showIntroduction = true;
      }else{
        this.showIntroduction = false;
      }
    })
  },
  methods: {
    Collect(Status){
      // 
      let cId = this.initData.data.assetId; // ID
      let cName = this.initData.data.assetName; // 
      let cStatus = Status ; // 
      xha.collect(cId, cName, cStatus);
    },
    init() {
      this.art = this.initData.data;
      this.eleId = this.art.assetId;
      if (this.queryFavorite()) {
        // 
        this.sc = true;
        this.ss = false;
      } else {
        this.sc = false;
        this.ss = true;
      }
      if (this.isSouth) {
        // 
        this.showNan = true;
      } else {
        // 
        this.showNan = false;
        this.mPlay();
      }
    },
    startScroll() {
      // 
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
        setTimeout(() => {
          $(".sl2").text();
          let p_title = $(".sl1").text();
          console.log("" + p_title.length);
          if (p_title.length >= 11) {
            console.log("");
            let speed = 40;
            let that = this;
            let ss = $('.ss')[0];
            let tab = $(".Bona .text-page")[0];
            let tab1 = $(".Bona .text-page>div:eq(0)")[0];
            let tab2 = $(".Bona .text-page>div:eq(1)")[0];
            tab.style.width = (tab1.offsetWidth*2 + 100)+'px';
            tab.style.textOverflow = "clip";
            tab2.innerHTML = tab1.innerHTML;
            this.timer = setInterval(function() {
              that.Marquee(ss, tab1, tab2);
            }, speed);
          }
        }, 10);
      });
    },
    Marquee(ss, tab1, tab2) {
      // console.log('offsetWidth:'+tab1.offsetWidth+'+++++++++++'+'scrollLeft:'+ss.scrollLeft)
      if (tab1.offsetWidth <= ss.scrollLeft) {
        ss.scrollLeft = 0;
      } else {
        ss.scrollLeft ++;
      }
    },
    changeEpisode(episode) {
      // JS
      console.log("" + episode);
      this.$store.commit("GET_EPISODE", episode - 1); //  
    },
    preViewCallback(args) {
      // 
      if (this.isAuth) {
        console.log("!");
        this.play(true, !this.isAuth, "preView");
      } else {
        console.log("!");
        this.removeCss(); // 
        this.isShow = false;
        yh.stop(); // 
        this.$emit("controllMeg", "", "", "preViewEnd", "");
      }
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      }
    },
    queryFavorite() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      if (storage) {
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.art.assetId) {
            return true;
          }
        }
      } else {
        return false;
      }
    },
    //
    Coll() {
      //        asset_id, collect_type, type
      try {
        let pageId = this.art.assetId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 3, 1);
      } catch (error) {
        console.log(error);
      }
    },
    //
    NoColl() {
      try {
        let pageId = this.art.assetId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 3, 2);
      } catch (error) {
        console.log(error);
      }
    },
    KeyEnter() {
      if (this.op == 0) {
        if (this.isAuth) {
          // Toast({message:"",position:"bottom"});
        } else {
          if (this.showNan) {
          } else {
            yh.stop(); // 
          }
          this.removeCss();
          this.isShow = false;
          this.$emit("controllMeg", false, "order", "vipOrder", "");
        }
      } else {
        if (this.itNum == 1) {
          if (!this.sc) {
            this.sc = true;
            this.ss = false;
            let Tvscc = {
              assetId: this.art.assetId,
              assetName: this.art.assetName,
              assetType: this.art.assetType,
              assetImg: this.art.assetImg,
              episodes: this.art.episodes,
              broadcast: this.art.broadcast,
              score: this.art.score,
              coverimage: this.art.coverimage,
              specialName: this.art.specialName,
              layout: this.art.layout,
              layoutUrl: this.jsonUrl
            };
            let storage = localStorage.getItem("PersonMovies");
            if (storage) {
              storage = JSON.parse(storage);
            } else {
              storage = [];
            }
            storage.push(Tvscc);
            localStorage.setItem("PersonMovies", JSON.stringify(storage));
            this.Coll();
            this.Collect(1)
          } else {
            this.sc = false;
            this.ss = true;
            let storage = JSON.parse(localStorage.getItem("PersonMovies"));
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findindex()) {
                json.push(storage[i]);
              }
            }
            localStorage.setItem("PersonMovies", JSON.stringify(json));
            this.NoColl();
            this.Collect(2)
          }
        } else if (this.itNum == 2) {
          if (this.showNan) {
          } else {
            yh.stop(false);
          }
          let info = {
            title: this.art.assetName,
            msg: this.art.description,
            alert: true
          };
          this.isShow = false;
          this.$emit("alertM", info);
          this.removeCss();
        } else if (this.itNum == 0) {
          // yh.toFull(); // 
          if (this.showNan) {
          } else {
            this.play(true, !this.isAuth, "fullSceen");
          }
          console.log("");
        }
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 
    },
    findindex() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      for (let i = 0; i < storage.length; i++) {
        if (storage[i].assetId == this.art.assetId) {
          return i;
        }
      }
    },
    addCss() {
      $("#b" + this.itNum).addClass("change");
    },
    removeCss() {
      $("#b" + this.itNum).removeClass("change");
      $(".advert").removeClass("change");
    },
    right() {
      if (this.op == 0) {
        this.removeCss();
        $(".advert").addClass("change");
      } else {
        if(this.itNum == 1 && !this.showIntroduction)return;
        if (this.itNum == 2) return;
        this.removeCss();
        this.num = 1;
        this.itNum++;
        this.addCss();
      }
    },
    left() {
      if (this.op == 0) {
        this.itNum = 0;
        this.op = 1;
        this.addCss();
        $(".advert").removeClass("change");
      } else {
        if (this.itNum == 0) return;
        $(".advert").removeClass("change");
        this.removeCss();
        this.num = 1;
        this.itNum--;
        this.addCss();
      }
    },
    down() {
      if (this.op == 0) {
        this.op = 1;
        this.itNum = 1;
        $(".advert").removeClass("change");
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    up() {
      if (this.itNum == 0) return;
      this.op = 0;
      this.removeCss();
      let eleH = this.cEleOffsetTop(".btn-sc");
      this.$emit("eleScroll", "seriesTop", "up", eleH);
      $(".advert").addClass("change");
    },
    mPlay() {
      let obj = { left: 57, top: 72, width: 672, height: 377 };
      yh.MaxPlay(JSON.stringify(obj));
    },
    play(bud, auth, orderStatus) {
      console.log(bud+"-----"+auth+"------"+orderStatus);
      let obj = {};
      let met = yh.playHistory(this.initData.data.assetId);
      console.log("" + met);
      if (c.isAllNull(met)) {
        // 
        console.log("ID0");
        this.$store.commit("GET_EPISODE", 0); // vuex
        obj = {
          index: 0,
          startTime: 0,
          jsonUrl: this.jsonUrl,
          buder: bud,
          preView: auth,
          callback: "preViewCallback"
        };
      } else {
        if (typeof met == "string") {
          met = JSON.parse(met);
        }
        let indexNum = met.episode - 1;
        let numWatchTime = parseInt(met.watchTime);
        if (auth) {
          if (orderStatus == "orderBack") {
            console.log(",0");
              if(this.isAuth==false){ // 
                let episode =  met.episode - 1;
                let fee = this.art.itemList[episode].fee;
                if(fee==0||fee=="0"){
                  console.log(",");
                  return false;
                }
              }
            numWatchTime = 0;
          }else{
            let episode =  met.episode - 1;
            let fee = this.art.itemList[episode].fee;
                if(fee==0||fee=="0"){
                  console.log(",");
                  numWatchTime = 0;
                  indexNum = 0;
                }
          }
        }else{
          console.log("");
        }
        console.log(":" + indexNum);
        this.$store.commit("GET_EPISODE", indexNum); // vuex
        obj = {
          index: indexNum,
          startTime: numWatchTime,
          jsonUrl: this.jsonUrl,
          buder: bud,
          preView: auth,
          callback: "preViewCallback"
        };
      }
      try {
          // BC
          let catId =  this.category[this.navpos].catId;
          let catName = this.category[this.navpos].catName;
          let catCode = this.category[this.navpos].catCode;
          if(this.EnterType == 0){
            catName = '';
            catId = '00';
            catCode = 'search'
          }
        c.logCsSplice(this.EnterType,catId,catName,this.productId,this.art.providerMemo.split(',')[0],catCode,this.eleId,this.art.assetName,(this.episode+1),this.art.itemList.length,this.FilmType);
      } catch (error) {
          console.log(''+error);
      }
      yh.play(JSON.stringify(obj));
      // this.preViewCallback();//   
    },
    updateStast(bl, pos, isEnd) {
      console.log(bl, pos, isEnd);
      if (bl) {
        if (this.showNan) {
          // 
        } else {
          if (pos == "desc") {
            // 
            console.log("");
            setTimeout(() => {
              // 
              this.play(false, !this.isAuth, "desc");
            }, 100);
          } else if (pos == "updateUrl") {
            console.log("");
            this.$nextTick(function() {
              setTimeout(() => {
                // 
                // this.play(false, !this.isAuth, "updateUrl");
              }, 100);
            });
          } else if (pos == "moveTop") {
            console.log("");
            if (yh.getPlayStatus()) {
              console.log("");
            } else {
              setTimeout(() => {
                this.play(false, !this.isAuth, "moveTop");
              }, 100);
            }
          }
        }
        this.$nextTick(function() {
          this.isShow = bl;
          // keyDo
          this.num = 0;
          if (this.isSouth) {
            this.itNum = 1;
            // 
          } else {
            this.itNum = 0;
          }
          if (pos == "desc") {
            // 
            this.itNum = 2;
          }
          this.addCss();
        });
      } else {
        this.removeCss();
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 100;
      return realHeight;
    },
    isAuthStatus(pos, status) {
      // BMS 
      if (pos === "payInit") {
        console.log("" + this.isAuth);
        this.play(false, false, "paySuccess"); //  
        this.initScores = require("../../assets/orderBy.png");
        this.num = 0;
        if (this.isSouth) {
          this.itNum = 1;
        } else {
          this.itNum = 0;
        }
        this.addCss();
        this.isShow = true;
      } else if (pos == "orderBack") {
        console.log("");
        if(status){
          this.isShow = true;
        }
        this.op = 1;
        this.num = 0;
        if (this.isSouth) {
          this.itNum = 1;
        } else {
          this.itNum = 0;
        }
        this.$nextTick(function() {
          setTimeout(() => {
            // 
            this.play(false, !this.isAuth, "orderBack");
          }, 100);
        });
        if(status){
          this.addCss();
        }
      } else if (pos == "AuthResult") {
        if (status == "paid") {
          console.log("!" + status);
          this.initScores = require("../../assets/orderBy.png");
          this.play(true, false, "init");
        } else if (status == "nopay") {
          console.log("!" + status);
          if (this.isSouth) {
            this.initScores = require("../../assets/south_payfor.png");
          } else {
            this.initScores = require("../../assets/payfor.png");
          }
          this.play(true, true, "init");
        } else if (status == "isfree") {
          console.log("!" + status);
          this.initScores = require("../../assets/nopay.png");
          console.log("");
          this.play(true, false, "init");
        }
        
      }
    }
  },
  computed: {
    ...mapGetters([
      "category",
      "behindParams",
      "isHTTPS",
      "navpos",
      "isSouth",
      "episode"
    ])
  },
  watch: {
    initData() {
      if(this.initData.data.description.length>45){
        this.showIntroduction = true;
      }else{
        this.showIntroduction = false;
      }
      this.EnterType = localStorage.getItem('EnterType') || 1;
      this.art = this.initData.data;
      this.eleId = this.art.assetId;
      this.isShow = false;
      $(".btn-sc").removeClass("change");
      if (this.queryFavorite()) {
        this.sc = true;
        this.ss = false;
      } else {
        this.sc = false;
        this.ss = true;
      }
      window.clearInterval(this.timer);
      this.timer = null;
      // this.startScroll();
    }
  },
  beforeRouteLeave(to, from, next) {
    try {
      window.clearInterval(this.timer);
      this.timer = null;
    } catch (error) {
    } finally {
      next();
    }
  }
};
</script>



