window.biCallback = function () {
    // console.log("~");
}

function combineArgs(){
    let sb = "";
    let host = "http://dx-log.hilooktv.com:8002/yh?";
    let curl = "";
    for (let i = 0; i < _gaq.length;i++){
        sb += _gaq[i].join("=") + '&';
    }
    curl = host+sb;

    _gaq = [];
    _gaq.push(['sys_v', yh.sys_v]); // 
    _gaq.push(['model', yh.model]); // 
    _gaq.push(['soft_v', yh.soft_v]); // 
    _gaq.push(['device_id', yh.device_id]); // 
    _gaq.push(['site_id', yh.siteId]); // id
    _gaq.push(['user_id', yh.userId]); // id
    if (isSouth) {
      _gaq.push(['log_type', '1-1']); // 
    } else {
      _gaq.push(['log_type', '1-2']); // 
    }
    _gaq.push(['create_time', c.formatDate(new Date().getTime())]); // 
    _gaq.push(['operator_id', yh.operator_id]); // ID1 2 3 4
    _gaq.push(['terrace_id', yh.terrace_id]); // ID(1 2)
    _gaq.push(['content_id', yh.content_id]); // (1 2 3 4 5 6 7 8)
    _gaq.push(['brand', yh.brand]); // 
    _gaq.push(['mode', yh.mode]); // 
    _gaq.push(['apk_version', yh.apk_version]); // apk
    _gaq.push(['reserve_group', yh.reserve_group]); // 
    _gaq.push(['collect_type', yh.collect_type]); // 
    _gaq.push(['v', '3']);

    //  console.log("statistics:"+curl);
    yh.proxyHttpRequest(curl, 'biCallback');
}


